import Navbar from "../../components/Navbar";
import Image from "next/image";

export default function Home() {
  return (
    <div className="min-h-screen w-full bg-[url('/images/bg_one.jpg')] bg-cover bg-center">
      <Navbar />

      <h1 className="text-center font-bold text-black big-text -mt-10">
        DIVINITY
      </h1>
      <p className="text-center font-bold text-black text-[15px] sm:text-[25px] -mt-20">
        EXCLUSIVE FOULARD COLLECTION
      </p>
      <div className="mt-5 flex justify-center">
        <Image
          src="/images/ds_labels.png"
          alt="DS Labels"
          width={550} // adjust size as needed
          height={0} // adjust size as needed
        />
      </div>
    </div>
  );
}
